import React, { useEffect, useState, memo } from "react";
import "blip-toolkit/dist/blip-toolkit.css";
import { getApplication } from "./api/applicationService";
import { showToast, withLoading } from "./api/commomService";
import { PageHeader } from "components/PageHeader";
import { CommonProvider } from "contexts/CommonContext";
import { PageTemplate } from "components/PageTemplate";
import { BlipTabs } from 'blip-toolkit'
import { BdsIcon } from 'blip-ds/dist/blip-ds-react';
import LogoCloneBots from './static/images/parse_cloneBots_v2.png';
import Agenda from 'components/Config/index';

import Doc from 'components/Doc/index'

function App() {

    const [application, setApplication] = useState({ shortName: '', accessKey: '', key: '', tenantId: '', emailOwner: '', email: '' })

    useEffect(async () => {
        withLoading(async () => {

            const { accessKey, shortName, tenantId, emailOwner } = await getApplication();

            const KEY = 'Key ' + btoa(`${shortName}:` + atob(accessKey));
            setApplication({ shortName, accessKey, key: KEY, tenantId, emailOwner });

            localStorage.setItem('plugin-agenda-key', KEY);
            localStorage.setItem('plugin-tenant-id', tenantId);
            showToast({
                type: "success",
                message: "Extesão carregada com sucesso"
            });

            new BlipTabs('tab-nav');
        });

    }, [])


    const title = `Extensão Agenda`;
    const logo = LogoCloneBots;



    return (


        <>
            <CommonProvider>

                <div id="main" className="App">
                    <PageHeader title={title} logo={logo} />
                    <PageTemplate title={title}>
                        <div id="tab-nav" className="bp-tabs-container">
                            <ul className="bp-tab-nav">

                                <li>
                                    <a href="#" data-ref="configVtex">Agenda</a> <BdsIcon theme="outline" type="icon" name="calendar" />
                                </li>
                                <li>
                                    <a href="#" data-ref="doc">Documentação</a> <BdsIcon theme="outline" type="icon" name="file-txt" />
                                </li>
                            </ul>

                            <div className="bp-tab-content fl w-1000" data-ref="configVtex">
                                <Agenda data={application.tenantId}/>
                            </div>
                            <div className="bp-tab-content fl w-1000" data-ref="doc">
                                <Doc />
                            </div>


                        </div>
                    </PageTemplate>
                </div>
            </CommonProvider>

        </>

    )
}

export default App